import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function priceToOperatingCashFlowRatioTtm(tableName: string) {
  const title = "P/OCF Ratio (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["priceToOperatingCashFlowRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Price per share vs. operating cash flow.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about P/OCF Ratio?`,
              value:
                info.row.original.financialData.companyRatios?.data
                  ?.priceToOperatingCashFlowRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            info.row.original.financialData.companyRatios?.data?.priceToOperatingCashFlowRatioTtm
          }
        />
      )
    },
  })
}

export { priceToOperatingCashFlowRatioTtm }
